<template lang="pug">
    .share-user(@click.stop="stop")
        .users
            we-userThumb
            
        .wrapper-share-user(ref="wrapperShareUser")
            button.button-share-user(@click="openShare")
            input.input-text(placeholder="Buscar")

            .list-search
                .user(v-for="user in 5")
                    .photo
                    .infos
                        span.name Vital Lima
                        span.count-task 2 task

</template>

<script>
    import WeUserThumb from '@/components/common/We-UserThumb.vue'

    export default {
        name: 'w-shareUser',
        props: ['currentPage'],
        components:{
            'we-userThumb': WeUserThumb
        },
        data () {
            return {
                msg: 'Welcome to Your Vue.js App'
            }
        },
        methods: {
            stop(){},
            openShare(){
                document.querySelector('.main-wrapper').setAttribute('data-share', this.currentPage)
            },
            closeShare(){
                document.querySelector('.main-wrapper').removeAttribute('data-share');
            }
        }
    }
</script>
<style lang="stylus" scope src="@/assets/styles/common/ShareUser.styl"></style>


