<template lang="pug">
    .content-task
        .group
            .header-content
                .users
                    we-userThumb.master
                    we-shareUser(currentPage="modal-task")

                .wrapper-options(ref="options")
                    button.button-options(@click="toggleOptions") Opções
                    button.button-back(@click="toggleOptions")
                    
                    .buttons
                        button.button-hashtag Hashtags
                        button.button-arquivos Aquivos
                        button.button-lembrete Lembretes

                        .more
                            button.button-more Mais Opções

                            .buttons-more
                                button.button-item Duplicar
                                button.button-item Excluir

            .content-text
                .content-title
                    we-editable(
                        class="title",
                        :content="currentTask.content.title",
                        @update="currentTask.content.title = $event"
                    )

                    .buttons
                        button.button-add Salvar
                        button.button-cancel Cancelar

                .content-description(v-if="currentTask.content.description")
                    we-editable(
                        class="description",
                        :content="currentTask.content.description",
                        @update="currentTask.content.description = $event"
                    )

                    .buttons
                        button.button-add Salvar
                        button.button-cancel Cancelar

                .timeline
                    span.current-date {{currentTask.content.date}}
                    .others
                        span.date Criado dia 21/2018 hás 8h

            .comments(v-if="currentTask.comments")
                .comment(v-for="comment in currentTask.comments", :class="comment.master ? 'master' : ''")
                    .button-like
                        .emojis
                            button.button-emoji
                            ul.list
                                li.emoji-1
                                li.emoji-2
                                li.emoji-3
                                li.emoji-4
                                li.emoji-5

                        .like Curtir

                    .info-user(v-if="!comment.master")
                        .photo
                        .name {{comment.user.name}}
                        .date-created {{comment.date}}

                    .text
                        p {{comment.text}}
        .footer
            .add-comment
                we-editable(
                    :content="contentComment.text",
                    @update="contentComment.text = $event", 
                    placeholder="Adicionar tarefa"
                )

                .buttons
                    button.button-add Comentar
                    button.button-cancel Cancelar

            button.button-concluir-task Concluir Tarefa
</template>

<script>
    import Boards from '@/components/boards/boards.js'
    import WeEditable from '@/components/common/We-Editable.vue'
    import WeShareUser from '@/components/common/We-ShareUser.vue'
    import WeUserThumb from '@/components/common/We-UserThumb.vue'

    export default {
        name: 'we-contentTask',
        components:{
            'we-editable': WeEditable,
            'we-shareUser': WeShareUser,
            'we-userThumb': WeUserThumb
        },
        data () {
            return {
                currentTask: null,
                contentComment: {
                    text: null
                },
                msg: 'Welcome to Your Vue.js App'
            }
        },
        created(){
            this.getTask();
        },
        methods:{
            getTask(){
                let currentBoard = null;
                let currentCol = null;

                let idBoard = this.$route.params.idBoard;
                let idCol = this.$route.params.idCol;
                let idTask = this.$route.params.idTask;

                for (var i = 0; i < Boards.boards.length; i++) {
                    currentBoard = Boards.boards[i];

                    if (currentBoard.id == idBoard) {
                        for (var i = 0; i < currentBoard.cols.length; i++) {
                            currentCol = currentBoard.cols[i]
                            if (currentCol.id == idCol) {
                                for (var i = 0; i < currentCol.tasks.length; i++) {
                                    if (currentCol.tasks[i].id == idTask) {
                                        this.currentTask = currentCol.tasks[i];
                                    }
                                }
                            }
                        }
                    }
                }
            },
            toggleOptions(){
                this.$refs.options.classList.toggle('show-options');
            }
        }
    }
</script>
<style lang="stylus" src="@/assets/styles/common/modais/ContentTask.styl"></style>


