<template lang="pug">
    .editable(contentEditable='true' @input="update")
</template>

<script>
    export default {
        name: 'w-editable',
        props:['content'],
        data () {
            return {
                msg: 'Welcome to Your Vue.js App'
            }
        },
        mounted:function(){
            this.$el.innerText = this.content;
        },
        methods:{
            update:function(event){
                this.$emit('update',event.target.innerText);
            }
        }
    }
</script>
<style lang="stylus" scope src="@/assets/styles/common/Editable.styl"></style>


