<template lang="pug">
    .note(@click="openNote")
        .container
            .content-text
                h2.title {{data.title}}
                p.description {{data.description}}

            .footer
                .date-edit 8 horas atrás

</template>

<script>
    export default {
        name: 'we-note',
        data () {
            return {
                msg: 'Welcome to Your Vue.js App'
            }
        },
        methods: {
            openNote(){
                let checkDrop = this.$el.getAttribute('data-drag');
                
                if (!checkDrop) {
                    this.$router.push({
                        name: 'notes',
                        params: {
                            idNote: this.data.id
                        }
                    })
                }
            }
        },
        props: ['data']
    }
</script>
<style lang="stylus" scope src="@/assets/styles/notes/Note.styl"></style>


