<template lang="pug">
	.wrapper-created-task
		.overlay(@click="close")
		button.button-trash
		.group
			.users-task
				we-userThumb.master
				we-shareUser(currentPage="created-task")

			.created
				.content-created
					we-editable.title(
						:content="contentTask.title",
						@update="contentTask.title = $event", 
						placeholder="O que é pra fazer?"
					)
					we-editable.descrition(
						:content="contentTask.description",
						@update="contentTask.description = $event", 
						placeholder="Descrição.."
					)

					.buttons(v-if="activeButtons")
						button.button-list Lista
						button.button-checklist Lista
						button.button-image Lista
				
				.footer-created(v-if="activeButtons")
					.deadlines
						button.button-today(@click="deadline(1, $event.target)").active Hoje
						button.button-tomorrow(@click="deadline(2, $event.target)") Amanhã
						button.button-date(@click="deadline(3, $event.target)")
							.place Data e hora
							.inputs-deadline
								input.date(placeholder="16/05/2018")
								span as
								input.time(placeholder="18:30")

					button.button-save Salvar em minhas tarefas
					button.button-cancel Cancelar

</template>

<script>
	import MasterUser from '@/master-user.js'
	import WeEditable from '@/components/common/We-Editable.vue'
	import WeShareUser from '@/components/common/We-ShareUser.vue'
	import WeUserThumb from '@/components/common/We-UserThumb.vue'

	export default {
		name: 'w-created-task',
		components:{
			'we-editable': WeEditable,
			'we-shareUser': WeShareUser,
			'we-userThumb': WeUserThumb
		},
		data () {
			return {
				activeButtons: false,
				contentTask: {
					title: null,
					description: null,
				},
				masteruser: MasterUser,
				msg: 'Welcome to Your Vue.js App'
			}
		},
		watch:{
			'contentTask.title'(old, current){
				this.checkInputs();
			},
			'contentTask.description'(old, current){
				this.checkInputs();
			}
		},
		methods:{
			checkInputs(){
				if (this.contentTask.title || this.contentTask.description != '') {
					this.activeButtons = true;
				}else{
					this.activeButtons = false;
				}
			},
			close(){
				this.$router.go(-1)
			},
			deadline(type, el){
				let buttons = document.querySelectorAll('.deadlines button')
				for (var i = 0; i < buttons.length; i++) {
					buttons[i].classList.remove('active')
				}

				el.classList.add('active');
			}
		}
	}
</script>
<style lang="stylus" scope src="@/assets/styles/created-task/CreatedTask.styl"></style>


