<template lang="pug">
	.content-note
		.modal-note(ref="modalNote", :data-convert="this.currentNote.convert")
			.group
				.header-content
					.user
						we-userThumb.master
						.content-user
							.text
								h3.name {{this.currentNote.user.master ? 'Você' : this.currentNote.user.name}}
								span.date {{this.currentNote.date}}
						
						we-shareUser(currentPage="modal-note")
						
					.wrapper-options(ref="options")
						button.button-options(@click="toggleOptions") Opções
						button.button-back(@click="toggleOptions")
						
						.buttons
							button.button-hashtag Hashtags
							button.button-arquivos Aquivos
							button.button-lembrete Lembretes

							.more
								button.button-more Mais Opções

								.buttons-more
									button.button-item Item
				.content-text
					.content-title
						we-editable(
							class="title",
							:content="this.currentNote.title",
							@update="this.currentNote.title = $event"
						)

						.buttons
							button.button-add Salvar
							button.button-cancel Cancelar
						
					.content-description
						we-editable(
							class="description",
							:content="this.currentNote.description",
							@update="this.currentNote.description = $event"
						)

						.buttons
							button.button-add Salvar
							button.button-cancel Cancelar

			.footer
				button.button-convert-task(@click="openConvert") Converter em tarefa

		.modal-convert
			.group
				.header-content
					.users
						we-userThumb.master
						we-shareUser(currentPage="modal-convert")
						
					.change-board
						.place(:data-value="selecChangeBoard")
						select.select(v-model="selecChangeBoard", @change="changeBoardSelect")
							option(v-for="board in boards", :data-id="board.id", :value="board.title") {{board.title}}

				.content-text
					.content-title
						we-editable(
							class="title",
							:content="this.currentNoteConvert.title",
							@update="this.currentNoteConvert.title = $event"
						)

						.buttons
							button.button-add Salvar
							button.button-cancel Cancelar
						
					.content-description
						we-editable(
							class="description",
							:content="this.currentNoteConvert.description",
							@update="this.currentNoteConvert.description = $event"
						)

						.buttons
							button.button-add Salvar
							button.button-cancel Cancelar

				.deadlines
					button.button-today(@click="deadline(1, $event.target)").active Hoje
					button.button-tomorrow(@click="deadline(2, $event.target)") Amanhã
					button.button-date(@click="deadline(3, $event.target)")
						.place Data e hora
						.inputs-deadline(@click.stop="stopTheEvent")
							input.date(size="10", placeholder="16/05/2018")
							span as
							input.time(size="5", placeholder="18:30")
				
			.footer
				button.button-save(@click="saveConvert") Salvar em {{currentBoard.title}}
				button.button-cancel(@click="closeConvert") Cancelar
</template>

<script>
	import Notes from '@/components/notes/notes.js'
	import Boards from '@/components/boards/boards.js'
	import MasterUser from '@/master-user.js'
	import WeEditable from '@/components/common/We-Editable.vue'
	import WeShareUser from '@/components/common/We-ShareUser.vue'
	import WeUserThumb from '@/components/common/We-UserThumb.vue'

	export default {
		name: 'we-contentNote',
		components:{
			'we-editable': WeEditable,
			'we-shareUser': WeShareUser,
			'we-userThumb': WeUserThumb
		},
		data () {
			return {
				currentBoard: {
					id: null, 
					title: null
				},
				masterUser: MasterUser,
				notes: Notes.notes,
				boards: Boards.boards,
				selecChangeBoard: 'Outro Quadro',
				currentNote: null,
				currentNoteConvert: null,
				msg: 'Welcome to Your Vue.js App'
			}
		},
		created(){
			this.getNote();
		},
		watch:{
		},
		mounted(){
			this.getBoard(this.$route.params.id);
		},
		methods: {
			saveConvert(){
				let tempCurrentBoard = null;
				let tempIdNewTask = null;

				for (var i = 0; i < this.boards.length; i++) {
					if (this.boards[i].id == this.currentBoard.id) {
						tempCurrentBoard = this.boards[i];
					}
				}

				if (tempCurrentBoard.cols[0].tasks.length) {
					let lastTaskId = tempCurrentBoard.cols[0].tasks[tempCurrentBoard.cols[0].tasks.length-1].id;
					tempIdNewTask = lastTaskId+1;
				}else{
					tempIdNewTask = 1;
				}
				
				let tempTask = {
					id: tempIdNewTask,
					users: [
						{
							master: true,
							photo: this.masterUser.infos.photo,
							name: this.masterUser.infos.name
						}
					],
					content: {
						title: this.currentNoteConvert.title,
						description: this.currentNoteConvert.description,
						date: 'Atrasado 2 dias',
						list: 0,
						comments: 0,
						anexos: 0
					},
					comments:[]
				};

				tempCurrentBoard.cols[0].tasks.push(tempTask)
				this.closeConvert();
				this.setStatusConvert();
			},
			openConvert(){
				this.$el.classList.add('show-convert')
			},
			closeConvert(){
				this.$el.classList.remove('show-convert')
			},
			changeBoardSelect(e){
				let tempBoardId = e.target.options[e.target.options.selectedIndex].getAttribute('data-id');
				this.getBoard(tempBoardId)
			},
			getBoard(id){
				let tempBoardId = id;

				for (var i = 0; i < this.boards.length; i++) {
					if (this.boards[i].id == tempBoardId) {
						this.currentBoard.id = tempBoardId;
						this.currentBoard.title = this.boards[i].title;
					}
				}
			},
			setStatusConvert(){
				this.$refs.modalNote.setAttribute('data-convert', true);
				this.currentNote.convert = true;
			},
			getNote(){
				let idNote = this.$route.params.idNote;

				for (var i = 0; i < Notes.notes.length; i++) {
					if (Notes.notes[i].id == idNote) {
						this.currentNote = Notes.notes[i];
						this.currentNoteConvert = Notes.notes[i];
					}
				}
			},
			stopTheEvent(){
				console.log('stop');
			},
			deadline(id, el){
				let buttons = document.querySelectorAll('.deadlines button')
				
				for (var i = 0; i < buttons.length; i++) {
				    buttons[i].classList.remove('active')
				}

				el.classList.add('active');
			},
			toggleOptions(){
				this.$refs.options.classList.toggle('show-options');
			}
		}
	}
</script>
<style lang="stylus" src="@/assets/styles/common/modais/ContentNote.styl"></style>


