import Vue from 'vue'
import Router from 'vue-router'

import Modal from '@/components/common/modais/We-Modal.vue'
import Notes from '@/components/notes/We-Notes.vue'
import Created from '@/components/created-task/We-CreatedTask.vue'

import WeBoards from '@/components/boards/We-Boards.vue';

Vue.use(Router)

export default new Router({
	routes: [
		{
			path: '/boards/:id',
			name: 'boards',
			component: WeBoards,
			children: [
				{
					path: 'task/:idBoard:idCol:idTask',
					name: 'task',
					component: Modal
				},
				{
					path: 'note/:idNote',
					name: 'notes',
					component: Modal
				}
				,
				{
					path: 'created',
					name: 'created',
					component: Created
				}
			]
		}
	]
})



