<template lang="pug">
    #app
        w-nav
        .main-wrapper
            router-view(:key="$route.fullPath")
            w-notes
</template>

<script>
    import Vue from 'vue'
    import WeNav from '@/components/common/We-Nav.vue';
    import WeNotes from '@/components/notes/We-Notes.vue';

    export default {
        name: 'App',
        components:{
            'w-nav': WeNav,
            'w-notes': WeNotes
        }
    }
</script>

<style lang="stylus" src="@/assets/styles/App.styl"></style>
<style lang="stylus" scoped src="@/assets/styles/MainWrapper.styl"></style>


