<template lang="pug">
    .main-nav
        h1.logo

        .items
            .group
                .item.hashtags
                    .icon
                    .text Hashtags
                .item.people
                    .icon
                    .text People
                .item.report
                    .icon
                    .text Reports

            .group
                .item.arquivados(@click="openArquivados")
</template>

<script>
    export default {
        name: 'w-nav',
        data () {
            return {
                msg: 'Welcome to Your Vue.js App'
            }
        },
        methods: {
            openArquivados(){
                document.querySelector('.boards').classList.toggle('show-arquivados');
            }
        }
    }
</script>
<style lang="stylus" scope src="@/assets/styles/common/ManNav.styl"></style>


