<template lang="pug">
    .modal
        .overlay(@click="close")
        .wrapper-modal
            we-content-task(v-if="$route.params.type === 'task'")
            we-content-note(v-if="$route.params.type != 'task'")
</template>

<script>
    import WeContentNote from '@/components/common/modais/We-ContentNote.vue';
    import WeContentTask from '@/components/common/modais/We-ContentTask.vue';

    export default {
        name: 'we-modal',
        components:{
            'we-content-task': WeContentTask,
            'we-content-note': WeContentNote
        },
        data () {
            return {
                msg: 'Welcome to Your Vue.js App'
            }
        },
        created(){
            let element = document.querySelector('body');
            element.addEventListener('click', (e) => {
                if (e.target.offsetParent.className != 'wrapper-share-user') {
                    document.querySelector('.main-wrapper').removeAttribute('data-share');
                }
            });
        },
        moutend(){
            console.log('oi');
        },
        methods: {
            close(){
                this.$router.go(-1)
            }
        }
    }
</script>
<style lang="stylus" scope src="@/assets/styles/common/modais/Modal.styl"></style>


