<template lang="pug">
	.boards
		.header-boards
			.group-user
				.user-content
					.photo
					.welcome
						h2.title Bom dia Vital.
						.count-tasks
							span.tasks 2 tarefas
							span.masrcacoes 3 marcações

				.notifications
					.icon-count
						span 2
					.content
						.icon
						p.text Você tem 2 compromissos por hoje

			.group-buttons
				button.button-created(@click="openCreated") Criar Tarefa
				button.search

		.wrapper-board(ref="wrapperboard")
			.header-board
				.wrap-title
					h2.title {{currentBoards.title}}
					.lines
						.line(:style="{'background-color' : currentBoards.color}")
						.line(:style="{'background-color' : currentBoards.color}")

				button.toggle(@click="showBoards") {{this.isActive ? 'Fechar' : 'Alterar quadro'}}

			.list-boards
				.wrapper-listboards
					.board(:style="{'background-color' : board.color}", v-for="board in boards", :class="board.id == currentBoards.id ? 'active' : ''", @click="goBoard(board.id)")
						.header
							.icon-active(:style="{'background-color' : board.color}")
							.options
								button.button-options
								.list
									button.button-editar Editar
									button.button-remove Remover
						.content
							h3.title {{board.title}}
							span.count-tasks 32 tasks

					.create-board(ref="createBoard", @click="openCreatedBoard")
						.content-text
							we-editable.title(
								ref="editable",
							    :content="contentBoard.title",
							    @update="contentBoard.title = $event", 
							    placeholder="Digite o titulo"
							)
						
						.colors
							button.button-color(@click="setColor($event.target)", v-for="(color, index) in colors", :data-color="color.code", :ref="'color-'+index", :style="{'background-color' : color.code}")

						.buttons
							button.button-created(@click.stop="createdBoard") Criar
							button.button-cancel(@click.stop="closeCreatedBoard") Cancelar

						h4.title Criar Quadro

			we-colsboard(:cols="currentBoards.cols", :idBoard="currentBoards.id")
		
		draggable(
			class="wrapper-arquivados",
			@end="end",
			v-model="currentBoards.arquivados",
			:options="{group:'cards', handle: '.header-arquivados', forceFallback: true, animation: 150, scrollSpeed: 0, scrollSensitivity:0}"
		)
			.header-arquivados
				h3.title Concluidas do quadro - {{currentBoards.title}}
				span.description Arraste para cá suas tarefas concluidas
			
			//- we-task(v-for="(task, index) in col.tasks", :key="index", :ref="task.id", :idCol="col.id", :idBoard="currentIdBoard", :data="task")
			we-task(v-for="(task, index) in currentBoards.arquivados", :key="index", :ref="task.id", :data="task")
			//- .card(v-for="(task, index) in arquivados", :key="index", :ref="task.id") {{task}}

		router-view(:key="$route.fullPath")
</template>

<script>
	import Boards from '@/components/boards/boards.js'
	import WeColsBoard from '@/components/boards/We-ColsBoard.vue';
	import draggable from 'vuedraggable'
	import WeEditable from '@/components/common/We-Editable.vue'
	import WeTask from '@/components/boards/We-Task.vue'

	export default {
		name: 'we-boards',
		components:{
			'we-task': WeTask,
			'we-editable': WeEditable,
		    'we-colsboard': WeColsBoard,
		    'draggable': draggable
		},
		props: ['data'],
		data () {
			return {
				colors:[
					{
						code:'#290062'
					},
					{
						code:'#756AFF'
					},
					{
						code:'#F68381'
					},
					{
						code:'#F9AB00'
					},
					{
						code:'#83DEF3'
					}
				],
				isActive: false,
				boards: Boards.boards,
				contentBoard: {
					color: '#290062',
					title: null
				},
				currentBoards: null
			}
		},
		created(){
			for (var i = 0; i < this.boards.length; i++) {
				if (this.boards[i].id == this.$route.params.id) {
					this.currentBoards = this.boards[i];
				}
			}
		},
		mounted(){
			document.querySelector('.button-color').setAttribute('data-active', true);
		},
		methods:{
			openCreatedBoard(){
				this.$refs.createBoard.classList.add('add-board');
			},
			closeCreatedBoard(){
				this.$refs.createBoard.classList.remove('add-board');
			},
			createdBoard(){
				if (this.contentBoard.title) {
					let lastBoardId = this.boards[this.boards.length-1].id;
					
					let tempObj = {
						id: lastBoardId+1,
						color: this.contentBoard.color,
						title: this.contentBoard.title,
						cols: []
					}

					this.boards.push(tempObj);
					this.closeCreatedBoard();

					this.contentBoard.color = '#290062';
					this.contentBoard.title = null;
					this.$refs.editable.$el.innerHTML = '';
				}
			},
			setColor(el){
				let elsColors = document.querySelectorAll('.button-color');
				this.contentBoard.color = el.getAttribute('data-color');

				for (var i = 0; i < elsColors.length; i++) {
					elsColors[i].removeAttribute('data-active');
				}

				el.setAttribute('data-active', true);
			},
			openCreated(){
				this.$router.push({
				    name: 'created'
				})
			},
			goBoard(idBoard){
				this.$router.push({
				    name: 'boards',
				    params: {
				        id: idBoard
				    }
				})
			},
			end(item){
				console.log('oi');
			},
			showBoards(){
				this.isActive = !this.isActive;
				this.$refs.wrapperboard.classList.toggle('show-boards')
			},
			getBoards(){

			}
		}
	}
</script>
<style lang="stylus" src="@/assets/styles/boards/Boards.styl"></style>


